.main-header__toggle {
  position: absolute;
  top: 24px;
  right: 0;
  z-index: 1;
  display: block;
  width: 24px;
  height: 24px;
  cursor: pointer;
  border: none;
  padding: 0;
  background-color: transparent;
}
.main-header__toggle:hover {
  opacity: 0.8;
}
.main-header__toggle:focus {
  opacity: 0.8;
}
